body {
  padding: 0 15px;
}

.container-new {
  max-width: 1200px;
  width: 100%;
}

@media only screen and (max-width: 1023px) {
  header {
    min-width: 320px;
    height: 320px;
  }
}

@media only screen and (max-width: 1023px) {
  header .bg-img {
    height: 320px;
  }
}

@media only screen and (max-width: 1023px) {
  header .container-new {
    height: 320px;
  }
}

@media only screen and (max-width: 1023px) {
  header .container-new, .owl-carousel, .owl-carousel .owl-stage-outer {
    height: 320px;
  }
}

@media only screen and (max-width: 767px) {
  .navbar-collapse {
    text-align: center;
    width: 100%;
    position: absolute;
    top: 90px;
    padding: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .navbar-brand {
    margin-left: 0;
  }
}

@media only screen and (max-width: 767px) {
  .navbar-nav h1 {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .pull-left, .pull-right {
    float: none !important;
  }
}

@media only screen and (max-width: 767px) {
  .nav-right {
    text-align: center;
  }
}

@media only screen and (max-width: 767px) {
  .navbar-nav {
    margin: 0;
    padding-top: 0;
  }
}

@media only screen and (max-width: 767px) {
  .navbar-nav li {
    float: none;
  }
  .navbar-nav li a {
    margin: 0;
  }
  .navbar-nav li span {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .nav-right {
    margin-right: 0;
    display: flex;
    justify-content: center;
    flex-direction: column-reverse;
  }
}

.menu-mobile-cross {
  display: none;
}

@media only screen and (max-width: 767px) {
  .menu-mobile-cross {
    display: inline-block;
    position: absolute;
    top: 28px;
    right: 20px;
    width: 33px;
    height: 33px;
  }
  .menu-mobile-cross span {
    position: absolute;
    top: 5px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross span {
    transform: rotate(-45deg);
    background-color: #fff;
  }
  .menu-mobile-cross span:before {
    content: '';
    position: absolute;
    top: 10px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross span:before {
    transform: rotate(45deg);
    top: 10px;
    background-color: #fff;
  }
  .menu-mobile-cross span:after {
    content: '';
    position: absolute;
    top: 21px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: top, opacity, background-color 0.3s;
  }
  .is-open .menu-mobile-cross span:after {
    opacity: 0;
    top: 0;
    background-color: #fff;
  }
}

.navbar {
  margin: 0 -15px;
}

@media only screen and (max-width: 1023px) {
  .movie-block {
    height: 100%;
    margin: 0 21px;
  }
}

@media only screen and (max-width: 1023px) {
  .movie-block:last-child {
    margin-right: 21px;
  }
}

@media only screen and (max-width: 1023px) {
  .movie-block:first-child {
    margin-left: 21px;
  }
}

.movie-block p {
  margin-bottom: 30px;
}
