body {
	padding: 0 15px; }
.container-new {
	max-width: 1200px;
	width: 100%; }
header {
	@include md {
		min-width: 320px;
		height: 320px; } }
header .bg-img {
	@include md {
		height: 320px; } }
header .container-new {
	@include md {
		height: 320px; } }
header .container-new, .owl-carousel, .owl-carousel .owl-stage-outer {
	@include md {
		height: 320px; } }
.navbar-collapse {
	@include sm {
		text-align: center;
		width: 100%;
		position: absolute;
		// right: -800px
		top: 90px;
		padding: 20px; } }
.navbar-brand {
	@include sm {
		margin-left: 0; } }
.navbar-nav h1 {
	@include sm {
		display: none; } }
.pull-left, .pull-right {
	@include sm {
		float: none !important; } }
.nav-right {
	@include sm {
		text-align: center; } }
.navbar-nav {
	@include sm {
		margin: 0;
		padding-top: 0; }
	@include sm {
		li {
			float: none;
			a {
				margin: 0; }
			span {
				display: none; } } } }
.nav-right {
	@include sm {
		margin-right: 0;
		display: flex;
		justify-content: center;
		flex-direction: column-reverse; } }
.menu-mobile-cross {
	display: none;
	@include sm {
		display: inline-block;
		position: absolute;
		top: 28px;
		right: 20px;
		width: 33px;
		height: 33px;
		span {
			position: absolute;
			top: 5px;
			width: 33px;
			height: 3px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: transform 0.5s;
			.is-open & {
				transform: rotate(-45deg);
				background-color: #fff; }
			&:before {
				content: '';
				position: absolute;
				top: 10px;
				width: 33px;
				height: 3px;
				margin-bottom: 10px;
				background-color: #fff;
				cursor: pointer;
				transition: transform 0.5s;
				.is-open & {
					transform: rotate(45deg);
					top: 10px;
					background-color: #fff; } }
			&:after {
				content: '';
				position: absolute;
				top: 21px;
				width: 33px;
				height: 3px;
				margin-bottom: 10px;
				background-color: #fff;
				cursor: pointer;
				transition: top, opacity, background-color 0.3s;
				.is-open & {
					opacity: 0;
					top: 0;
					background-color: #fff; } } } } }
.navbar {
	margin: 0 -15px; }
